.banner {
  margin-top: 10vh;
  height: 80vh;
  margin-bottom: -80px;
  color: white;
  object-fit: contain;
}
.banner_contents {
  margin-left: 80px;
  padding-top: 80px;
}
banner_title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}
.bnanner_description {
  width: 45rem;
  line-height: 1;
  padding: 1rem 0;
  font-size: 1rem;
  max-width: 360px;
  height: 80px;
}
.banner_button {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-size: 1.2rem;
  font-weight: 700;
  border-radius: 0.2vw;
  padding: 1rem 3rem;
  margin-right: 2rem; /* Space to the right of each button */

  /* background-color: rgba(51, 51, 51, 0.5); */
}
/* .banner_button:nth-child(2) {
  margin-left: 2rem; /* Add space to the left of the second button 
} */
.play {
  color: black;
  background-color: #fff;
}
.Banner_button:hover {
  color: black;
  background-color: #c6c6c6;
  transition: a110.2s;
}
.banner_fadebutton {
  height: 40vh;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgb(37, 37, 37, 0.91),
    #111
  );
}
