.header_outer_container {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: center;
  /* background-color: rgb(140, 112, 112); */
  background-color: rgb(20, 20, 20);
  /* background-color: transparent; */
  height: 10vh;
  color: aliceblue;
  transition: all 0.5s;
  transition-timing-function: ease-in;
  z-index: 1;
}
.header_container {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* background-color: yellow; */
}
.header_left ul {
  width: 140%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  /* background-color: blue; */
}
.header_right ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
}
